@import url(https://fonts.googleapis.com/css2?family=Over+the+Rainbow&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
    text-decoration: none;
    color: white !important;
}

a:focus,
a:active {
    color: #ed6c82 !important;
    border: unset !important;
}
.row {
    margin-right: unset !important;
    margin-left: unset !important;
}
.row {
    margin-right: unset !important;
    margin-left: unset !important;
}
.project-image {
    width: 50%;
    height: 250px;
    padding: 20px;
    box-shadow: 0 10px 20px rgba(97, 94, 94, .26);
    margin: 20px;
}

.project-image:hover {
    transform: rotate(0deg);
    transform: scale(1.2);
    z-index: 10;
    box-shadow: 0 10px 20px rgba(97, 94, 94, .26);
}

.projectParagraph {
    position: absolute;
    top: 27%;
    text-align: center;
    margin: 10px;
    width: 100%;
}

.anchorproject {
    cursor: pointer;
    position: absolute;
    bottom: 39%;
    margin: 10px;
    left: 45%;
    /* width: 0%; */
    color: #ed6c82 !important;
    text-align: center;
}
* {
    margin: 0;
    padding: 0;
    outline: none;
    border: none;
    box-sizing: border-box;
}

*:before,
*:after {
    box-sizing: border-box;
}

html,
body {
    min-height: 100%;
}

body {
    text-align: center;
    /* background-image: radial-gradient(mintcream 0%, gray 100%); */
    font-family: "Trebuchet MS";
}

.wrap {
    width: 300px;
    height: 350px;
    margin: 50px auto;
    border-radius: 20px;
    border: solid 8px #2c3033;
    background: white;
    box-shadow: 0 0 0px 1px rgba(255, 255, 255, .4), 0 0 0px 3px #2c3033;
}

.header {
    width: 100%;
    height: 50px;
    border-radius: 10px 10px 0 0px;
    background-image: linear-gradient(#687480 0%, #3b4755 100%);
    font: bold 18px/50px arial;
    text-align: center;
    color: white;
    box-shadow: inset 0 -2px 3px 2px rgba(0, 0, 0, .4), 0 2px 2px 2px rgba(0, 0, 0, .4);
}

span {
    display: block;
    width: 100%;
    height: 90%;
}

.wrap-list {
    height: 30%;
    width: 50%;
    margin-left: 15%;
    margin-right: 15%;
}

input[type="checkbox"] {
    position: relative;
    top: 14px;
    right: 12px;
    float: right;
}

input[type="checkbox"]:checked+label {
    text-decoration: line-through;
    color: rgba(0, 0, 0, .3);
}

.list {
    list-style: none;
    color: rgba(0, 0, 0, .7);
}

.list li:nth-child(odd) {
    background: rgba(0, 0, 50, .1);
}

.list li {
    float: left;
    width: 100%;
}

label {
    padding: 10px;
    /* cursor: pointer; */
    float: left;
    width: 95%;
    transition: all .3s ease;
}

input {
    border: 1px solid lightgray;
}

.tabs {
    background: transparent;
    height: 50px;
    width: 8rem;
    font-size: 1.2rem;
    color: #ed6c82 !important;
    font-family: cairo;
    text-align: center;
    /* margin: auto; */
    letter-spacing: 2px;
    border: 1px solid #ed6c82;
    padding: 8px;
    margin: 10px;
    cursor: pointer;
    font-weight: 700;
    text-transform: uppercase;
}

.tabs:hover {
    background-color: #ed6c82;
    color: #1f2737 !important;
    border: 1px solid #ed6c82;
}
.workhistory p {
    font-size: 22px !important;
}
html {
    scroll-behavior: smooth;
}


